 div#container {
  width: 1000px;
  height: 600px;
  margin: auto;
  background-color: #007EA7;
  border-radius: 10px;
}

.chart {
  width: 100%;
  height: 100%;
}

.bar {
  fill: white;
  cursor: pointer;
}

text {
  font-size: 12px;
  fill: #fff;
}

.path {
  stroke: white;
}

 line {
  stroke: rgb(165, 40, 40);
  z-index: 1;
  stroke-width: 3;
}

.grid path {
  stroke-width: 0;
}

.grid .tick line {
  stroke: #9FAAAE;
  stroke-opacity: 0.3;
}

text.divergence {
  font-size: 14px;
  fill: rgb(247, 247, 247);
  font-weight: bold;
  cursor: pointer;
  position: absolute;
  z-index: -1;

}

text.value {
  font-size: 14px;
}

text.title {
  font-size: 22px;
  font-weight: 600;
}

text.label {
  font-size: 14px;
  font-weight: 400;
}

text.source {
  font-size: 10px;
}

#what1, #what2, #what3, #what4 {
  height: 100%;
  width: 100%;
  background: transparent;
}

.graph-title body {
background: #007EA7;
 color:white;
}

.graph-title body h1 {
  text-align: center;
  font-size: 37px;
}

.graph-title body p {
  text-align: justify;
  letter-spacing: 2px;
  font-size: 14px;
}

.graph-div {
  width: 800px;
  height: 320px;
  background-image: url(images/hydroP.jpg);
  position: relative;
  left:2%;
}

.back2 {
  font-size: 16px;
  cursor: pointer;
}